$(document).ready(function () {
  $(".c-media-dropdown .item").click(function () {
    $(".resource-media-wrap").removeClass("active");
    $("." + $(this).attr("data-id")).addClass("active");
  });

  //data picker
  $(".js-date-picker").each(function () {
    $(this).flatpickr({
      enableTime: false,
      dateFormat: "F, Y ",
    });
  });
});

$('button[data-bs-toggle="tab"]').on("shown.bs.tab", function (e) {
  var paneTarget = $(e.target).attr("data-bs-target");
  var $thePane = $(".tab-pane" + paneTarget);
  var paneIndex = $thePane.index();
  if (
    $thePane.find(".swiper-container").length > 0 &&
    0 === $thePane.find(".swiper-slide-active").length
  ) {
    dieCastingexploresolution[paneIndex].update();
  }
});
